.navmenustyle{
  top:0;
  left:0;
  padding:1rem 0;
  width:100%;
  text-decoration: none;
  text-align: center;
  display:inline-block;
  
}
li{
  display:inline-block;
  margin:5px;
  
  
}

*{
  text-decoration: none;
}
.active{
  color:rgb(247, 6, 6);
}
.mobile-menu-icon{
  position:absolute;
  right:1rem;
  top:1rem;
  width:4rem;
  cursor:pointer;
  outline:none;
  display:none;
}
.closeNavIcon{
  display:none;
}
@media only screen and (max-width:768px){
  
  .mobile-menu-icon{
    display:block;

  }
  .hide-item{
    transform:translateY(calc(-100% - var(--top)));

  }
  .navItems{
    --top:1rem;
    transition:.1s ease transform;
    max-width: 300px;
    border-radius: 12px;
    width:90px;
    padding:2rem;
    background-color: gray;
    position:absolute;
    right:1rem;
    top:var(--top);
   
  }
  .closeNavIcon{
    display:block;
    width:3rem;
    margin:0 0 0 auto;
    cursor:pointer;
  }
  .hide-item{
    display:none;
  }
  
}
img{
  width:33%;
  height:auto;  
}
.center{
  margin-left:auto;
  margin-right:auto;
  display:block;
  width:50%;
}